.controls {
    position: fixed;
    top: 0;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    height: 60px;
    background-color: #111;
}

.controls button {
    padding: 8px 18px;
    margin: 5px 2px;
    background-color: #EEE;
}

.play-position {
    text-align: center;
    color: #DDD;
    width: 80px;
    padding: 10px;
    margin: 5px 10px;
    background-color: #444;
}


